<template>
  <v-container class="mt-10">
    <v-row align="center" justify="center">
      <v-col cols="12" sm="10">
        <v-card class="elevation-6 mt-10">
          <v-window v-model="step">
            <v-window-item :value="1">
              <v-row>
                <v-col cols="12" md="6">
                  <v-card-text class="mt-12">
                    <h4 class="text-center">Inicie su sesión con su usuario</h4>
                    <h6 class="text-center grey--text">
                      Inicie sesión con sus credenciales <br />
                      Indicando su correo y respectiva contraseña
                    </h6>
                    <v-row align="center" justify="center">
                      <v-col cols="12" sm="8">
                        <v-text-field v-model="correo" label="Email" outlined dense color="black" autocomplete="false"
                          class="mt-12" />
                        <v-text-field v-model="pass" label="Password" outlined dense color="black" autocomplete="false"
                          type="password" />
                        <v-row>
                          
                          <v-col cols="12" sm="12" class="mb-6">
                            <span class="Subtitle 1 blue--text">Forgot password</span>
                          </v-col>
                        </v-row>
                        <v-btn color="orange" class="black--text" dark block tile @click="login()">Iniciar Sesion
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-col>
                <v-col cols="12" md="6" class="orange rounded-bl-xl">
                  <div style="text-align: center; padding: 180px 0">
                    <v-card-text class="black--text">
                      <h3 class="text-center">No tienes aun usuario?</h3>
                      <h6 class="text-center">
                        Registrese para que pueda prestar equipos de computo de
                        la Biblioteca SENA - San Gil
                      </h6>
                    </v-card-text>
                    <div class="text-center">
                      <v-btn tile outlined dark class="black--text" @click="step++">Registrese</v-btn>
                    </div>
                  </div>
                </v-col>
              </v-row>
            </v-window-item>
            <v-window-item :value="2">
              <!-- Pantalla de registro -->

              <v-row>
                <v-col cols="12" md="4" class="orange rounded-br-xl">
                  <div style="text-align: center; padding: 180px 0">
                    <v-card-text class="black--text">
                      <h3 class="text-center">Ya esta registrado?</h3>
                      <h6 class="text-center">
                        Inicie sesión para que registre su prestamo y edite su
                        perfil
                      </h6>
                    </v-card-text>
                    <div class="text-center">
                      <v-btn tile outlined dark class="black--text" @click="step--">Iniciar Sesión</v-btn>
                    </div>
                  </div>
                </v-col>

                <v-col cols="12" md="4" class="mt-4">
                  <v-card-text class="mt-4">
                    <v-row align="center" justify="center">
                      <v-col cols="12" align="center" justify="center">
                        <h2 class="mb-11" justify="center">
                          Registre su cuenta
                        </h2>
                        <v-text-field label="Nombre Completo" outlined dense color="black" autocomplete="false" 
                        v-model="nombre"/>
                        <v-text-field label="Documento" outlined dense color="black" autocomplete="false" 
                        v-model="documento"/>
                        <v-text-field label="Email" outlined dense color="black" autocomplete="false" 
                        v-model="email"/>
                        <v-text-field label="Password" outlined dense color="black" autocomplete="false"
                          type="password" v-model="password"/>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-col>
                <v-col cols="12" md="4" class="mt-4">
                  <v-card-text class="mt-4">
                    <v-row align="center" justify="center">
                      <v-col class="cols-12" align="center">
                        <v-text-field label="Ficha" outlined dense color="blue" autocomplete="false" 
                        v-model="ficha"/>
                        <v-text-field label="Telefono" type="Number" outlined dense color="blue" autocomplete="false" 
                        v-model="telefono"/>
                        <v-text-field disabled v-model="estado" type="text" outlined dense color="blue" autocomplete="false"/>
                        <v-select :items="roles" label="Perfil" outlined dense v-model="perfil"></v-select>

                        <v-btn @click="registrar_usuario()" block class="orange black--text">Registrar</v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-col>
              </v-row>
            </v-window-item>
          </v-window>
        </v-card>
      </v-col>
    </v-row>
     <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-container>
  
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PageLogin",
  data() {
    return {
      step: 1,
      overlay:false,
      nombre: "",
      password: "",
      documento: "",
      telefono: "",
      email: "",
      perfil: "",
      ficha: "",
      estado:"Activo",
      roles: ["APRENDIZ", "ADMIN", "INSTRUCTOR", "ADMINISTRATIVO", "PUBLICO"],
      imagen:
        "https://us.123rf.com/450wm/apoev/apoev1903/apoev190300009/124806570-persona-hombre-de-marcador-de-posici%C3%B3n-de-foto-gris-en-camiseta-sobre-fondo-gris.jpg",
      valor: "",
      correo: "",
      pass: "",
      show1: false,
      errores: "",
    };
  },
  methods: {
    login() {
      this.overlay=true
      axios
        .post("https://cat-biblioteca.herokuapp.com/api/holder/login", {
          email: this.correo,
          password: this.pass,
        })
        .then((response) => {
          console.log(response.data.token);
          this.$store.dispatch("setToken", response.data.token);
          this.$store.dispatch("setDatos", response.data.holder);
          this.$store.dispatch("setFoto",response.data.holder.photo)
          console.log(this.$store.state.datos);
          this.overlay=false
          this.$router.push("/inicio");
        })
        .catch((error) => {
          console.log(error);
          if (this.correo === "" || this.pass === "") {
            Swal.fire({
              title: "<strong>Error en credenciales</strong>",
              icon: "error",
              html: `<h4>${error.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
          } else {
            Swal.fire({
              title: "<strong>Error en credenciales</strong>",
              icon: "error",
              html: `<h4>${error.response.data.msg}</h4>`,
              showCloseButton: true,
            });
          }
        });
    },

    registrar_usuario() {
      console.log(this.password);
      axios.post("https://cat-biblioteca.herokuapp.com/api/holder",
        {
          document: this.documento,
          name: this.nombre,
          email:this.email,
          password:this.password,
          phone:this.telefono,
          ficha:this.ficha,
          rol:this.perfil
        })
        .then(response =>{
          console.log(response);
           Swal.fire ({
            position: 'bottom-end',
            icon: 'success',
            title: 'Registro exitoso',
            showConfirmButton: false,
            timer: 2000
          })
          this.limpiarcajas()
        })
        .catch(error=>{
          console.log(error);
          Swal.fire({
              title: "<strong>Error en registro</strong>",
              icon: "error",
              html: `<h4>${error.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
        })
    },

    limpiarcajas(){
      this.nombre= "",
      this.password= "",
      this.documento= "",
      this.telefono= "",
      this.email= "",
      this.perfil= "",
      this.ficha= ""
    }
  },
};
</script>
<style scoped>
.v-application .rounded-bl-xl {
  border-bottom-left-radius: 300px !important;
}

.v-application .rounded-br-xl {
  border-bottom-right-radius: 300px !important;
}
</style>