<template>
    <v-app class="mt-16">
        <template>
            <v-container fluid class="mt-5 ">
                <v-row>
                    <v-col cols="6">
                        <v-card class=" elevation-0">
                            <h2>Holders</h2>
                        </v-card>
                    </v-col>
                    <v-col cols="6">
                        <v-card class=" elevation-0 d-flex justify-end">
                            <v-btn @click="nuevoHolder()">Nuevo Holder</v-btn>
                        </v-card>

                    </v-col>
                </v-row>
            </v-container>
            <v-data-table :headers="encabezados" :items="holders" :items-per-page="5" class="elevation-1 mt-5 mx-5">
                <template v-slot:[`item.opciones`]="{ item }">
                    <template  v-if="item.state==='1'">
                    <v-icon  class="mr-2" color="red" @click="editar(item)" > mdi-account-off</v-icon>
                    </template>
                    <template v-else>
                    <v-icon  class="mr-2" color="green" @click="editar(item)" >mdi-account-check</v-icon>
                    </template>
                </template>
                <template v-slot:[`item.state`]="{ item }">
                    <span class="green--text" v-if="item.state==='1'">Activo</span>
                    <span class="red--text" v-else>Inactivo</span>
                </template>
            </v-data-table>
        </template>

        <!-- Cuadro de dialogo para insertar holder -->

        <v-dialog v-model="dialog" persistent max-width="600px">

            <v-card>
                <v-card-title>
                    <span class="text-h5">Guardar Perfil</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12" sm="6" md="6">
                                <v-text-field label="Documento" type="number" v-model="documento"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="6">
                                <v-text-field label="Ficha" type="number" v-model="ficha"></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="Nombre Completo" v-model="nombre"></v-text-field>
                            </v-col>
                             <v-col cols="12">
                                <v-text-field label="Correo" v-model="correo"></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="Contraseña" type="password" v-model="pass"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6">
                                <v-select :items="['ADMIN', 'PUBLICO', 'APRENDIZ', 'INSTRUCTOR', 'ADMINISTRATIVO']" label="Perfil" v-model="perfil"></v-select>
                            </v-col>
                            
                        </v-row>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="cerrarDialog()">
                        Cerrar
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="nuevoHolder()">
                        Guardar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-app>
</template>

<script>
import axios from "axios"

export default {
    name: "PagePersona",
    data() {
        return {
            dialog: false,
            nombre:"",
            pass:"",
            documento:"",
            telefono:"",
            correo:"",
            perfil:"",
            ficha:"",
            encabezados: [
                {
                    text: 'Documento',
                    align: 'start',
                    sortable: false,
                    value: 'document',
                },
                { text: 'Nombre', value: 'name' },
                { text: 'Telefono', value: 'phone' },
                { text: 'Correo', value: 'email' },
                { text: 'Ficha', value: 'ficha', sortable: false, },
                { text: 'Rol', value: 'rol', sortable: false, },
                { text: 'Estado', value: 'state', sortable: false, },
                { text: 'Opciones', value: 'opciones', sortable: false },
            ],
            holders: []
        }
    },
    methods: {
        listarHolders() {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios.get("https://cat-biblioteca.herokuapp.com/api/holder", header)
                .then(response => {
                    console.log(response);
                    this.holders = response.data.holder
                })
                .catch(error => {
                    console.log(error);
                })
        },
        nuevoHolder() {
            this.dialog = true;
        },
        cerrarDialog() {
            this.dialog = false;
        },
        editar(item) {
            console.log(item.state);
            if (item.state==="1"){
                let header = { headers: { "x-token": this.$store.state.token } };
                axios.put(`https://cat-biblioteca.herokuapp.com/api/holder/unactivate/${item._id}`,{},header)
                .then(response => {
                    console.log(response);
                    this.listarHolders()
                })
                .catch(error=>{
                    console.log(error.response);
                })
            }else{
                let header = { headers: { "x-token": this.$store.state.token } };
                axios.put(`https://cat-biblioteca.herokuapp.com/api/holder/activate/${item._id}`,{},header)
                .then(response => {
                    console.log(response);
                    this.listarHolders()
                })
                .catch(error=>{
                    console.log(error.response);
                })
            }
        }
    },
    mounted() {
        this.listarHolders()
    }
}
</script>