import persona from "../pages/Persona.vue"
import equipo from "../pages/Equipo.vue"
import registro from "../pages/Registro.vue"
import login from "../pages/Login.vue"
import perfil from "../pages/Perfil.vue"
import inicio from "../pages/Inicio.vue"
import miequipo from "../pages/MiEquipo.vue"


export const routes = [
    {path:"/", component:login},
    {path:"/miequipo", component:miequipo, meta:{requiresAuth:true}},
    {path:"/inicio", component:inicio, meta:{requiresAuth:true}},
    {path:"/perfil", component:perfil,  meta:{requiresAuth:true}},
    {path:"/persona", component:persona, meta:{requiresAuth:true}},
    {path:"/equipo", name:"equipo", component:equipo, meta:{requiresAuth:true}},
    {path:"/registro", component:registro, meta:{requiresAuth:true}},
]