<template>
  <div class="mb-10 mt-16">
    <div v-if="$store.state.token !== ''">
    <v-container class="orange cont d-flex justify-center" style="height: 200px">
      <v-avatar color="white" size="150" class="ava">
        <v-img :src="imagen" @click="aumentar()"></v-img>
      </v-avatar>
    </v-container>
    <v-container class="d-flex justify-center" >
      <v-card class="mt-5 tar black white--text" width="500px" rounded="xl">
        <v-card-title> </v-card-title>
        <v-card-title class="d-flex justify-center mt-4">
          {{ nombre }}
        </v-card-title>
        <v-list class="d-flex justify-center">
          <v-list-item-group>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-school</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Soy {{ perfil }} de la ficha {{ ficha }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon> mdi-account </v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Documento: {{ documento }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon> mdi-email </v-icon>
              </v-list-item-icon>
              <v-list-item-title>Correo: {{ correo }}</v-list-item-title>
              <v-list-item-content> </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon> mdi-phone </v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Telefono: {{ telefono }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
        <v-card-actions>
          <v-btn color="orange " text @click="abrir()" class="ml-3">
            <v-icon>mdi-pencil</v-icon>
            Actualizar Perfil
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="orange " text @click="registrar_equipo()" class="ml-3">
            <v-icon>mdi-laptop</v-icon>
            <h4>Registrar equipo</h4>
          </v-btn>
        </v-card-actions>

      </v-card>

    </v-container>

    <!-- Cuadro de dialogo -->

    <v-dialog v-model="dialog">
      <div class="white" align="center" justify="center">
        <h1>Registre sus datos</h1>
        <v-container>
          <v-row>
            <v-col cols="12" md="6" class="mt-4">
              <v-card-text>
                <v-row align="center" justify="center">
                  <v-col cols="12" align="center" justify="center">
                    <v-text-field label="Nombre Completo" v-model="nombre" outlined dense color="black"
                      autocomplete="false" />
                    <v-text-field label="Documento" v-model="documento" outlined dense color="black"
                      autocomplete="false" />
                    <v-text-field label="Ficha" v-model="ficha" outlined dense color="blue" autocomplete="false" />
                    <v-text-field label="Telefono" v-model="telefono" type="Number" outlined dense color="blue"
                      autocomplete="false" />
                    <v-select v-model="perfil" :items="roles" label="Perfil" outlined dense></v-select>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
            <v-col cols="12" md="6">
              <v-card-text>
                <v-row align="center" justify="center">
                  <v-col class="cols-12" align="center">
                    <v-img :src="imagen" height="250" width="250" class="mb-8" style="border: 5px solid;">
                      <h2 v-if="imagen === ''">No imagen</h2>
                    </v-img>
                    <input type="file" @change="subir">
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
            <v-btn block class="orange black--text mb-6" @click="actualizar()">Actualizar Datos</v-btn>
          </v-row>
        </v-container>
      </div>
    </v-dialog>
    <v-dialog v-model="dialog2" height="400" width="300">
      <v-card class="mx-auto" max-width="344">
        <v-img :src="imagen" height="300px"></v-img>

        <v-card-title>
          {{ nombre }}
        </v-card-title>

        <v-card-actions>

          <v-spacer></v-spacer>

          <v-btn dense class="orange" elevation="2" @click="salir()">
            Salir
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </div>
      <v-container v-else>
      <v-row>
        <v-col> </v-col>
        <v-col class="d-flex justify-center">
          <v-img src="../assets/images/good-night-sweet-dreams.gif" height="250" width="250">
          </v-img>
        </v-col>
        <v-col> </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-alert outlined text type="error">
            <v-row align="center">
              <v-col class="grow">
                En el momento su sesion ha caducado ❗ Por favor inicie sesion para interactuar con los servicios de
                biblioteca
              </v-col>
              <v-col class="shrink">
                <v-btn @click="login()" outlined color="red">
                  <v-icon left>
                    mdi-login
                  </v-icon> Login
                </v-btn>
              </v-col>
            </v-row>
          </v-alert>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import axios from "axios"
import Swal from "sweetalert2";
export default {

  name: "PagePerfil",
  data() {
    return {
      roles: ["ADMIN", "PUBLICO", "APRENDIZ", "INSTRUCTOR", "ADMINISTRATIVO", "GUARDA","BIBLIOTECA"],
      dialog: false,
      dialog2: false,
      img: "",
      nombre: this.$store.state.datos.name,
      perfil: this.$store.state.datos.rol,
      documento: this.$store.state.datos.document,
      ficha: this.$store.state.datos.ficha,
      correo: this.$store.state.datos.email,
      telefono: this.$store.state.datos.phone,
      imagen: this.$store.state.datos.photo,
    };
  },
  methods: {
    login(){
      this.$router.replace("/")
    },
    abrir() {
      this.dialog = true;
    },
    aumentar() {
      this.dialog2 = true
    },
    salir() {
      this.dialog2 = false
    },
    subir(e) {
      this.img = e.target.files[0]
      console.log(this.img);
      let fd = new FormData();
      fd.append("archivo", this.img);
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(fd);
      axios.post(`https://cat-biblioteca.herokuapp.com/api/holder/uploadcloud/${this.$store.state.datos._id}`,
        fd, header)
        .then(response => {
          console.log(response.data.url);
          this.imagen=response.data.url
          this.$store.dispatch("setFoto",response.data.url)
        })
        .catch(error => {
          console.log(error);

        })
    },
    registrar_equipo() {
      console.log(this.$store.state.datos._id);
      /* this.$router.push(
        {
        name: "equipo", params: {
          id: this.$store.state.datos._id,
        }
      }) */
      this.$router.replace("/miequipo")
    },

    actualizar() {
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(this.$store.state.datos._id);
      axios.put(`https://cat-biblioteca.herokuapp.com/api/holder/${this.$store.state.datos._id}`,
        {
          document: this.documento,
          name: this.nombre,
          ficha: this.ficha,
          phone: this.telefono,
          rol: this.perfil
        }, header
      )
        .then(response => {
          console.log(response);
          Swal.fire({
            position: 'bottom-end',
            icon: 'success',
            title: 'Actualización exitosa',
            showConfirmButton: false,
            timer: 2000
          })
          this.dialog = false
        })
        .catch(error => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualización rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        })
    }

  },
  mount() {
    this.llenar_perfil()
  },

};
</script>

<style>
.ava {
  position: relative;
  top: 120px;

  border: 5px solid;
  z-index: 2;
}

.cont {
  border-radius: 0px 0px 50px 50px;
}

.cont2 {
  border-radius: 50px 50px 0px 0px;
  border: 2px solid;
  border-color: black;
}

.cont3 {
  border-radius: 0px 0px 50px 50px;
  border: 2px solid;
  border-color: black;
}
</style>

